/* App.css */

body {
    font-family: 'Arial', sans-serif;
    background-color: #f4f4f4;
    margin: 0;
    padding: 0;
    color: #333;
}

.App {
    max-width: 1200px;
    margin: 40px auto;
    padding: 20px;
    background-color: #ffffff;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
}

header {
    text-align: center;
    padding: 20px;
    background-color: #007BFF;
    color: #ffffff;
    border-radius: 10px 10px 0 0;
}

    header h1 {
        margin: 0;
        font-size: 2.5em;
    }

.weather-table {
    margin: 20px auto;
    width: 100%;
    border-collapse: collapse;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    overflow: hidden;
}

    .weather-table th,
    .weather-table td {
        padding: 15px;
        text-align: left;
        border-bottom: 1px solid #ddd;
    }

    .weather-table th {
        background-color: #007BFF;
        color: white;
        text-transform: uppercase;
    }

    .weather-table tr:nth-child(even) {
        background-color: #f9f9f9;
    }

    .weather-table tr:hover {
        background-color: #e9e9e9;
    }

.weather-chart {
    margin: 40px auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    max-width: 1000px;
    justify-content: center;
    align-items: center;
}

    .weather-chart h2 {
        text-align: center;
        font-size: 1.8em;
        color: #007BFF;
        margin-bottom: 20px;
    }

@media (max-width: 768px) {
    .App {
        margin: 20px;
    }

    .weather-table th, .weather-table td {
        padding: 10px;
    }

    .weather-chart {
        margin: 20px auto;
    }
}
